.MenuHeader
{
    min-block-size: 40px;

    inline-size: 100%;
    block-size: 7vh;

    display: flex;
    justify-content: space-between;
    align-items: center;

    position: fixed;
    top: 0px;
    z-index: 5;

    background-image: linear-gradient(var(--linear-gradient-primary));
}

.MenuHeader .Logo
{
    inline-size: 20%;
    block-size: 80%;

    object-fit: contain;
}

.MenuHeader .Menu
{
    inline-size: 10%;
    block-size: 40%;

    margin-right: 3%;
    border: none;
    
    background-image: url('./../../../assets/icons/Menu.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: var(--background-color-opacity-full);

}

.MenuHeader .Menu:active
{
    transform: scale(1.1);
}